<!--  -->
<template>
    <div class='order'>
           <div class='orderNav'>
               <p :class='[{"navLists":type==index},"navList"]' v-for='(item,index) in navList' :key='index' @click='navTab(index)' >{{item}}</p>
           </div>
           <div class="order-box" v-if='orderList.length!=0' >
               <div class='order-info' v-for='(item,index) in orderList' :key='index'>
                   <div class='top'>
                       <p class='order-time'>下单时间：{{item.orderTime}}</p>
                       <p class='order-status' v-if='item.orderStatu==1'>等待付款</p>
                       <p class='order-status' v-if='item.orderStatu==2'>等待发货</p>
                       <p class='order-status' v-if='item.orderStatu==3'>交易成功</p>
                       <p class='order-status' v-if='item.orderStatu==5'>已完成</p>
                   </div>
                   <div class='info' v-for='(items,indexs) in item.SlWxOrderItemTabList' :key='indexs'>
                       <img :src="items.productImg" alt="" class='info-img' >
                       <div class='info-fr'>
                           <p class='info-name'>{{items.productName}}</p>
                           <div class='fr-btm'>
                               <span class='info-money'>￥{{items.productPrice}}</span>
                               <span class='info-number'>x{{items.buyNum}}</span>
                           </div>
                       </div>
                   </div>
                   <div class='btm'>
                        <div>
                            <p class='info-jian'>共{{item.productNum}}件商品</p>
                            <p class='all-money'>合计：<span class='all-moneys'>￥{{item.totalPrice}}</span> </p>
                        </div>
                        <div>
                            <p class='infp-types' v-if='item.orderStatu==1' >取消订单</p>
                            <p class='infp-type' v-if='item.orderStatu==1'>去支付</p>
                            <p class='infp-type' v-if='item.orderStatu==2'>查看详情</p>
                            <p class='infp-type' v-if='item.orderStatu==3' >确认收货</p>
                            <p class='infp-type' v-if='item.orderStatu==5'>删除订单</p>
                        </div>
                      
                   </div>
               </div>
           </div>
           <div class='empty' v-else>
               <img src="../../../static/image/order_empty.png" alt="" class='empty-icon' >
           </div>
    </div>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';
    import {mapActions} from 'vuex'
    import {orderList} from '../../api/api'
    export default {
        //import引入的组件需要注入到对象中才能使用
        components: {},
        data() {
            //这里存放数据
            return {
                navList:['全部','待付款','待发货','待收货','已完成'],
                type:0,
                orderList:[]
            };
        },
        //监听属性 类似于data概念
        computed: {},
        //监控data中的数据变化
        watch: {},
        //方法集合
        methods: {
            ...mapActions(['headTitle','showBtm','goBack']),
           
            navTab:function(index){
                this.loading = this.$toast.loading();
                this.type=index
                this.request(index)
            },
            request:function(status){
                if(status==4){
                    status=5
                } else if(status==0){
                    status=''
                }
                orderList(status).then(response=>{
                     this.loading.hide();
                     this.orderList=response.data.data
                });
            }
            
        },
        //生命周期 - 创建完成（可以访问当前this实例）
        created() {
            this.headTitle('我的订单')
            this.showBtm(false)
            this.goBack(true)
            this.type=this.$route.params.type
        },
        //生命周期 - 挂载完成（可以访问DOM元素）
        mounted() {
            this.loading = this.$toast.loading();
           this.request(this.type)
        },
        beforeCreate() {}, //生命周期 - 创建之前
        beforeMount() {}, //生命周期 - 挂载之前
        beforeUpdate() {}, //生命周期 - 更新之前
        updated() {}, //生命周期 - 更新之后
        beforeDestroy() {}, //生命周期 - 销毁之前
        destroyed() {}, //生命周期 - 销毁完成
        activated() {}, //如果页面有keep-alive缓存功能，这个函数会触发
    }
</script>
<style lang='scss' scoped>
    //@import url(); 引入公共css类
    .order{
        margin-top:90px;
    }
    .orderNav{
        height:88px;
        width:100%;
        padding:0 3%;
        background:#ffff;
        margin-bottom:20px;
        display:flex;
        justify-content:space-around;
        align-items:center;
    }
    .navList{
        font-size:28px;
        color:rgb(105,105,105);
        line-height:88px;
    }
    .navLists{
        border-bottom:5px solid #f00;
        color:#000;
    }
    .order-info{
        width:94%;
        margin:0 auto;
        background:#fff;
        border-radius:10px;
        margin-bottom:20px;
        padding:0 3%;
    }
    .top{
        width:100%;
        display:flex;
        justify-content:space-between;
        height:88px;
        line-height: 88px;
        align-items:center;
    }
    .order-time{
        font-size:26px;
        color:rgb(78,78,78);
    }
    .order-status{
        font-size:26px;
        color:#f00;
    }
    .info{
        display:flex;
        height:190px;
    }
    .info-img{
        width:190px;
        height:190px;
        display:block;
        margin-right:16px;
    }
    .info-name{
        font-size:26px;
        color:#000;
        text-align: left;
    }
    .info-fr{
        height:190px;
        display:flex;
        justify-content:space-around;
        flex-direction: column;
        width:70%;
    }
    .fr-btm{
        display:flex;
        justify-content:space-between;
        align-items:center;
        font-size:28px;
        color:rgb(41,41,41);
    }
    .btm{
        display:flex;
        height:100px;
        align-items:center;
        font-size:24px;
        color:rgb(78,78,78);
        justify-content:space-between;
    }
    .btm div{
        display:flex;
         align-items:center;
    }
    .info-jian{
        color:rgb(78,78,78);
    }
    .all-money{
        margin-left:20px;
        color:rgb(78,78,78);
    }
    .all-moneys{
        color:#f00;
    }
    .infp-type{
        width:140px;
        height:50px;
        text-align: center;
        line-height:50px;
        border-radius:30px;
        background:#f00;
        color:#fff;
        margin-left:30px;
    }
    .infp-types{
        width:140px;
        height:50px;
        text-align: center;
        line-height:50px;
        border-radius:30px;
        border:2px solid rgb(201,201,201);
        margin-left:30px;
    }
    .empty{
        width:300px;
        height:300px;
        margin:0 auto;
        overflow:hidden;
        margin-top:200px;
    }
    .empty-icon{
        width:100%;
        height:100%;
    }
</style>


